import { useState } from "react";
import ModalGithub from "../components/ModalGithub";
import '../styles/Home.css';

export default function Home() {
    const [showModal, setShowModal] = useState(false);

    return (
        <>
        {/* Hero section */}
        <section className="hero d-flex flex-column justify-content-center align-items-center text-center text-light">
            <div className="overlay"></div>
            <div className="content">
                <h1 className="fw-bold">Bonjour, je suis Vladyslav Kharkovskyi</h1>
                <h2 className="mb-4">Dveloppeur web full-stack</h2>
                <button
                    className="btn btn-danger btn-lg"
                    onClick={() => setShowModal(true)}
                >
                    En savoir plus
                </button>
            </div>
        </section>
        {/* SECTION " propos" + "Mes comptences" */}
        <section className="container my-5">
            <div className="mx-auto shadow p-4 bg-white rounded row"
                style={{ maxWidth: "1000px" }}>
                {/* " propos" */}
                <div className="col-md-6 mb-4 mb-md-0">
                    <h3 className="mb-3"> propos</h3>
                    <img
                        src="/img/john-doe-about.jpg"
                        alt="John Doe"
                        className="img-fluid rounded mb-3"
                    />
                    <p>
                        Passionn par la cration numrique et les technologies du web, je conois et dveloppe des sites internet modernes, performants et accessibles.
                    </p>
                    <p>
                        Mon travail repose sur une approche centre sur lutilisateur : chaque projet commence par la comprhension des besoins et se termine par une interface claire et intuitive. Jaccorde une grande importance  la qualit du code,  la performance et  la compatibilit entre navigateurs.
                    </p>
                    <p>
                        En parallle du dveloppement front-end, je mintresse galement au back-end et  lintgration dAPI pour crer des applications web compltes.
                    </p>
                </div>
                {/* "Mes comptences" */}
                <div className="col-md-6">
                    <h3 className="mb-3">Mes comptences</h3>
                    <div className="mb-3">
                        <label>HTML5 90%</label>
                        <div className="progress">
                            <div
                                className="progress-bar bg-danger"
                                style={{ width: "90%" }}
                            ></div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <label>CSS3 80%</label>
                        <div className="progress">
                            <div
                                className="progress-bar bg-info"
                                style={{ width: "80%"}}
                            ></div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <label>JavaScript 70%</label>
                        <div className="progress">
                            <div
                                className="progress-bar bg-warning"
                                style={{ width: "70%"}}
                            ></div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <label>PHP 60%</label>
                        <div className="progress">
                            <div
                                className="progress-bar bg-success"
                                style={{ width: "60%"}}
                            ></div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <label>React 50%</label>
                        <div className="progress">
                            <div
                                className="progress-bar bg-primary"
                                style={{ width: "50%"}}
                            ></div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        {/* MODALE GITHUB */}
        {showModal && <ModalGithub onClose={() => setShowModal(false)} />}
        </>
    );
}